@use '../../index.scss' as v;

.main-nav {
  margin-bottom: 15px;
}

a {
  margin-top: 5px;
}

.navbar {
  position: relative;
}

.navbar a {
  cursor: pointer;
}

.navbar-brand {
  position: absolute;
  left: 50%;
  margin-left: -78px !important;
  display: block;
}

.navbar-collapse a,
.navbar-collapse button {
  box-shadow: inset 0 0 0 0 v.$secondary-color;
  color: v.$secondary-color !important;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.navbar-collapse a:hover,
.navbar-collapse button:hover {
  box-shadow: inset 100px 0 0 0 v.$secondary-color;
  color: white !important;
  border-radius: 5px;
  text-decoration: none !important;
}

.search-bar {
  margin-left: 10px;
}

.anim-search {
  opacity: 0;
  transition: 1s opacity;
}

.fade-in {
  opacity: 1;
}

.fade-out {
  opacity: 0;
}
